html, body {
  display: flex;
  height: 100%;
  width: 100%;
  margin: 0px;
  padding: 0px;
  background-color: black;
}


#root {
  height: 100%;
  flex: 1 1;
  display: flex;
}

.app-container {
  background-color: rgba(28, 28, 28);
  height: 100%;
  flex: 1 1;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.home-body {
  flex: 14 1;
  display: flex;
  align-items: flex-end;
  justify-content: flex-start;
  font-family: "Staatliches", cursive;
  font-weight: bold;
  color: rgb(243, 243, 243);
}

.home-sub {
  flex: 9 1;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  background-color: white;
  width: 100%;
  text-align: center;
}

.sub-title {
  overflow: hidden;
  white-space: nowrap;
  margin: 0 0 0 0 auto;
  width: 100%;
  letter-spacing: 0.15em;
  font-size: 3vmin;
  padding: 5vmin;
  font-family: "Montserrat", sans-serif;
}

.name-title {
  text-align: center;
  font-size: 15vmin;
  text-align: left;
  overflow: hidden;
  border-right: 0.05em solid orange;
  white-space: nowrap;
  margin: 0 0 0 0 auto;
  letter-spacing: 0.15em;
  -webkit-animation: typing 3s steps(40, end), blink-caret 0.75s step-end infinite;
          animation: typing 3s steps(40, end), blink-caret 0.75s step-end infinite;
}

@-webkit-keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

@-webkit-keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: rgb(243, 243, 243);
  }
}

@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: rgb(243, 243, 243);
  }
}

/**
font-family: 'Staatliches', cursive;
