.navbar-container {
    background: rgb(28,28,28);
    flex: 1 1;
    width: 100%;
    display: flex;
    flex-direction: row;
    height: 100px;
}

.home-button {
    background: rgb(28,28,28);
    flex: 1 1;
    display: flex;
    color: white;
    align-items: center;
    justify-content: center;
    font-family: 'Montserrat', sans-serif;
    font-weight: 800;
    transition: background 0.3s ease-in, color 0.3s ease-in;
}

.home-button:hover {
